.search {
   position: relative;
   width: 100%;
   height: 59px;
   max-width: 250px;

   &__input {
      font-size: 12px;

      width: 100%;
      height: 100%;
      padding: 20px 50px 20px 20px;

      background: #ededed;
      border-radius: 36px;
      border: #ededed;

      &:focus-visible {
         outline: #c9c9c9 solid 1px;
      }
   }

   &__button {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);

      padding: 0;
      width: 39px;
      height: 39px;
      justify-content: center;

      svg {
         stroke: white;
         height: 19px;
         width: 19px;
      }
   }
}
